.page-footer {
  background-color: $color-gray-200;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: row;
  }
}

.page-footer__logo {
  display: none;

  @media (min-width: $tablet-width) {
    display: inline;
  }
}

.page-footer__social-list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}

.page-footer__developer-label {
  font-size: 19px;
  font-weight: 700;
  line-height: 18px;
  color: $color-black;
}
